
/**
 * CommonFunctions
 * 
 * Alfredo Barrios
 */
 const CommonFunctions = {

	GetCaller:function(){
		//return navigator.appCodeName + " - " + navigator.product + " - " + navigator.appVersion + " - " + navigator.platform + " - " + navigator.onLine + " - " + navigator.language;
		return navigator.appCodeName + " - " + navigator.product + " - " + navigator.language;
	},
	EmailFormatMatch:function(param){
		var pattern = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/i;
		var res = param.match(pattern);
		return (param==res);
	},
	PasswordFormatMatch:function(param){
		var pattern = /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])([a-zA-Z0-9]{8,})$/g;
		var res = param.match(pattern);
		return (param==res);
	},
	GetUrlParameter: function(name) {
		name = name.replace(/[\[]/, '\\[').replace(/[\]]/, '\\]');
		var regex = new RegExp('[\\?&]' + name + '=([^&#]*)');
		var results = regex.exec(window.location.search);
		return results === null ? '' : decodeURIComponent(results[1].replace(/\+/g, ' '));
	},
	Void:function(){}

}
export default CommonFunctions;