const RestCaller = {
	GetRemoteDataAsync:function(ActionUrl, Arguments, PostGet, successCallBack, ErrorCallBack, TimeoutCallBack){
		try{
			var xmlhttp = new XMLHttpRequest();
			/*xmlhttp.onreadystatechange = function(){

			};*/
			xmlhttp.open(PostGet, ActionUrl, true);
			xmlhttp.timeout = 2000; // time in milliseconds
			xmlhttp.ontimeout = function (e) {
				TimeoutCallBack(e);
			  };
			xmlhttp.setRequestHeader("Content-Type", "application/json");
			xmlhttp.setRequestHeader("authentication", "wDo3rXrE/"); 
			xmlhttp.onreadystatechange = function(){
				if (this.readyState === 4 && this.status === 200) {
					successCallBack(this.responseText);
				}
			};
			xmlhttp.send(Arguments);
		}catch(e){
			ErrorCallBack(e);
		}
	}, 
	GetRemoteDataSync:function(ActionUrl, Arguments, PostGet, ErrorCallBack){
		try{
			var xmlhttp = new XMLHttpRequest();
			xmlhttp.open(PostGet, ActionUrl, true);
			xmlhttp.setRequestHeader("Content-Type", "application/json");
			xmlhttp.setRequestHeader("authentication", "wDo3rXrE/"); 
			xmlhttp.send(Arguments);
			return xmlhttp.responseText;
		}catch(e){
			ErrorCallBack(e);
		}
	},
	Fetch:function(ActionUrl, Arguments, PostGet, SuccessCallBack, ErrorCallBack){
		const restSrv = fetch(ActionUrl, {method:PostGet,
		headers: new Headers({
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			'authentication': 'wDo3rXrE/' // <-- Specifying the Content-Type
		}),
		body:Arguments}).then(
			(response) => response.text()
		).then(
			(responseText) => {
				//Success
				SuccessCallBack(JSON.parse(responseText));
			})
		.catch((error) => {
			ErrorCallBack(error);
		});
	},
	FetchMultipart:function(ActionUrl, Arguments, PostGet, SuccessCallBack, ErrorCallBack){
		const restSrv = fetch(ActionUrl, {method:PostGet,
		headers: new Headers({
			'authentication': 'wDo3rXrE/' // <-- Specifying the Content-Type
		}),
		body:Arguments}).then(
			(response) => response.text()
		).then(
			(responseText) => {
				//Success
				SuccessCallBack(JSON.parse(responseText));
			})
		.catch((error) => {
			ErrorCallBack(error);
		});
	}
}
export default RestCaller;