import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';
import CommonFunctions from '../../helpers/CommonFunctions';
import RestCaller from '../../helpers/RestCaller';
import { urlVerifyChangePasswordCode, urlSetNewPassword } from '../../helpers/external-vars';

class SetNewPasswordForm extends Component {

	constructor(props){
		super(props);
		let code = CommonFunctions.GetUrlParameter("code");
		this.state={
			requestCode:code,
			inputPassword:"",
			inputConfirmPassword:"",
		};
		this.submitButtonClickHndlr = this.submitButtonClickHndlr.bind(this);
		this.CheckCode();
	}

	CheckCode(){
		if(this.state.requestCode.trim()==="") {
			document.getElementById("formSetPassword").style.display = "none";
			document.getElementById("codeDoesNotExistMsg").style.display = "block";
			return false;
		}
		let Arguments = '{"platform":"web","caller":"' + CommonFunctions.GetCaller() + '","code":"' + this.state.requestCode + '"}';
		RestCaller.Fetch(urlVerifyChangePasswordCode, Arguments, 'POST', (responseData)=>{
			//Success
			this.evalRightCode(responseData);
		}, (error) =>{
			this.evalWrongCode(error);
		});
	}

	evalRightCode(data){
		document.getElementById('loadingImage').style.display = "none";
		if(data.code!=200){
			document.getElementById("formSetPassword").style.display = "none";
			document.getElementById("codeDoesNotExistMsg").style.display = "block";
		}
	}

	evalWrongCode(error){
		document.getElementById('loadingImage').style.display = "none";
		document.getElementById("formSetPassword").style.display = "none";
		document.getElementById("codeDoesNotExistMsg").style.display = "block";
		document.getElementById('unexpectedErrorMsg').style.display = "block";
	}

	
	Validation(){
		const {inputPassword,inputConfirmPassword} = this.state;
		if(!CommonFunctions.PasswordFormatMatch(inputPassword)){
			document.getElementById("lengthWronghMsg").style.display = "block";
			return false;
		}
		if(inputPassword!=inputConfirmPassword){
			document.getElementById("confirmDoesNotMatchMsg").style.display = "block";
			return false;
		}
		return true;
	}

	handleChange = event => {
		event.preventDefault();
		const { id, value } = event.target;
		switch(id){
			case "inputPassword":
				this.setState({
					inputPassword: value
				});
				break;
			case "inputConfirmPassword":
				this.setState({
					inputConfirmPassword: value
				});
				break;
			default:

		}
	}


	submitButtonClickHndlr(event){
		event.preventDefault();
		const {inputPassword,inputConfirmPassword} = this.state;
		document.getElementById("codeDoesNotExistMsg").style.display = "none";
		document.getElementById("fiilPasswordAndConfirmMsg").style.display = "none";
		document.getElementById("confirmDoesNotMatchMsg").style.display = "none";
		document.getElementById("lengthWronghMsg").style.display = "none";
		document.getElementById("unexpectedErrorMsg").style.display = "none";
		document.getElementById("successMsg").style.display = "none";
		if(!this.Validation()){
			return false;
		}
		document.getElementById('loadingImage').style.display='block';
		let Arguments = '{"code":"' + this.state.requestCode +'","newpassword":"' + inputPassword + '", "confirmpassword":"' + inputConfirmPassword +'"}';
		RestCaller.Fetch(urlSetNewPassword, Arguments, 'POST', (responseData)=>{
			//Success
			this.evalSuccessResponse(responseData);
		}, (error) =>{
			this.errorCallerBackend(error);
		});
	}

	evalSuccessResponse(data){
		document.getElementById('loadingImage').style.display = "none";
		if(data.code===200){
			document.getElementById('btnSubmit').style.display = "none";
			document.getElementById('successMsg').style.display = "block";
		}else{
			if(data.message==="ELAPSED_TIME")
				document.getElementById("codeDoesNotExistMsg").style.display = "block";
			else
				document.getElementById("unExpectedErrorMsg").style.display = "block";
		}
	}

	errorCallerBackend(error){
		document.getElementById('loadingImage').style.display = "none";
		document.getElementById('unexpectedErrorMsg').style.display = "block";
	}

	render(){
		return(
			<div class="row justify-content-center">
			<div class="col-lg-5">
				<div id="loadingImage" class="modalWindow">
					<div class="modalWindow-content">
						<img  src={require('../../assets/images/ajax-loader01.gif')} alt="" />
    				</div>
				</div>
				<div class="card shadow-lg border-0 rounded-lg mt-5">
					<div class="card-header"><h3 id="lblFormTitle" class="text-center font-weight-light my-4">
						<FormattedMessage id="setPwd.lblFormTitle" defaultMessage="Establece tu nueva contraseña" />
					</h3></div>
					<div id="formSetPassword" class="card-body">
						<div id="lblEnterPassword" class="small mb-3 text-muted"><FormattedMessage id="setPwd.lblEnterPassword" defaultMessage="Establece tu nueva contraseña" /></div>
						<form>
							<div class="form-group"><label id="lblinputPassword" class="small mb-1" for="inputPassword"><FormattedMessage id="setPwd.lblinputPassword" defaultMessage="Nueva Contraseña" /></label><input class="form-control py-4" id="inputPassword" type="password" autoFocus  onChange={this.handleChange} /></div>
							<div class="form-group"><label id="lblinputConfirmPassword" class="small mb-1" for="inputConfirmPassword"><FormattedMessage id="setPwd.lblinputConfirmPassword" defaultMessage="Confirma tu Nueva Contraseña" /></label><input class="form-control py-4" id="inputConfirmPassword" type="password" onChange={this.handleChange} /></div>
							<div class="form-group d-flex align-items-center justify-content-between mt-4 mb-0">
								<button  id="btnSubmit" className="btn btn-primary" onClick={this.submitButtonClickHndlr}>
									<FormattedMessage id="setPwd.btnSubmit" defaultMessage="Enviar" />
								</button>
							</div>
						</form>
					</div>
					<div class="alert alert-danger collapse" role="alert" id="codeDoesNotExistMsg">
						<FormattedMessage id="setPwd.codeDoesNotExistMsg" defaultMessage="El código no existe o ya no es valido ya." />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="fiilPasswordAndConfirmMsg">
						<FormattedMessage id="setPwd.fiilPasswordAndConfirmMsg" defaultMessage="Por favor llena el campo Nueva Contraseña y confirma la contraseña." />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="confirmDoesNotMatchMsg">
						<FormattedMessage id="setPwd.confirmDoesNotMatchMsg" defaultMessage="La contraseña y la confirmación no coinciden." />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="lengthWronghMsg">
						<FormattedMessage id="setPwd.lengthWronghMsg" defaultMessage="La contraseña debe tener por lo menos 8 caracteres, contener por lo menos una letra minúscula, contener por lo menos una letra mayúscula y contener por lo menos un número." />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="unexpectedErrorMsg">
						<FormattedMessage id="setPwd.unexpectedErrorMsg" defaultMessage="Ocurrió un error inesperado, por favor, vuelve a intentar más tarde." />
					</div>
					<div class="alert alert-success collapse" role="alert" id="successMsg">
					<a href='./login'><FormattedMessage id="setPwd.successMsg" defaultMessage="La contraseña se estableció con éxito, por favor, proporcione sus credenciales" /></a>
					</div>
				</div>
			</div>
		</div>
		);
	}
}

export default SetNewPasswordForm;