import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Home } from './components/Home';
import { About } from './components/About';
import { Contact } from './components/Contact';
import { NoMatch } from './components/NoMatch';
import { Layout } from './components/Layout';
import Workspace from './components/Workspace';
import ModifyProfileForm from './components/register/ModifyProfileForm';
import RegisterForm from './components/register/RegisterForm';
import RegisterConfirmForm from './components/register/RegisterConfirmForm';
import LoginForm from './components/credentials/LoginForm';
import RecoveryPasswordForm from './components/credentials/RecoveryPasswordForm';
import SetNewPasswordForm from './components/credentials/SetNewPasswordForm';
import { NavigationBar } from './components/NavigationBar';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

class App extends Component {
  render() {
    return(
      <React.Fragment>
        <NavigationBar />
        <Layout>
          <Router basename={'http:/localhost:8080/koatchy'}>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/about" component={About} />
              <Route exact path="/contact" component={Contact} />
              <Route exact path="/login" component={LoginForm} />
              <Route exact path="/register" component={RegisterForm} />
              <Route exact path="/recoveryPassword" component={RecoveryPasswordForm} />
              <Route exact path="/setNewPassword" component={SetNewPasswordForm} />
              <Route exact path="/registerConfirm" component={RegisterConfirmForm} />
              <Route exact path="/modifyProfile" component={ModifyProfileForm} />
              <Route exact path="/workspace" component={Workspace} />
              <Route component={NoMatch} />
            </Switch>
          </Router>
        </Layout>
      </React.Fragment>
    );
  }
}

export default App;
