import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';
import RestCaller from '../../helpers/RestCaller';
import { ulrLoginValidation } from '../../helpers/external-vars';
import CommonFunctions from '../../helpers/CommonFunctions';
import PersistData from '../../helpers/PersistData';
import 'bootstrap/dist/css/bootstrap.css';


class LoginForm extends Component {
	constructor(props){
		super(props);
		this.state={
			username:"",
			password:"",
		};
		this.submitButtonClickHndlr = this.submitButtonClickHndlr.bind(this);
	}

	handleChange = e => {
		e.preventDefault();
		const { id, value } = e.target;
		let formErrors = this.state.formErrors;
		switch(id){
			case "inputEmailAddress":
				this.setState(() => ({
					username: value,
				}));
				break;
			case "inputPassword":
				this.setState(() => ({
					password: value,
				}));
				break;
			default:

		}
		this.setState({formErrors, [id]: value}, ()=>{console.log("handleChange: " + id + " - " + value + " - " + this.state)});		
	}

	submitButtonClickHndlr(event){
		event.preventDefault();
		const { username, password } = this.state;

		document.getElementById('successMsg').style.display = "none";
		document.getElementById('emailEmptyMsg').style.display = "none";
		document.getElementById('passwordEmptyMsg').style.display = "none";
		document.getElementById('worngCredentialsMsg').style.display = "none";
		document.getElementById('unExpectedErrorMsg').style.display = "none";
		if(username.length<3){
			document.getElementById('emailEmptyMsg').style.display = "block";
			return;
		}
		if(password.length<3){
			document.getElementById('passwordEmptyMsg').style.display = "block";
			return;
		}
		document.getElementById('loadingImage').style.display='block';
		let Arguments = '{"platform":"web","caller":"' + CommonFunctions.GetCaller() + '","username":"' + document.getElementById("inputEmailAddress").value + '","password":"' + document.getElementById("inputPassword").value + '"}';
		RestCaller.Fetch(ulrLoginValidation, Arguments, 'POST', (responseData)=>{
			//Success
			this.evalSuccessResponse(responseData);
		}, (error) =>{
			this.errorCallerBackend(error);
		});
	}

	evalSuccessResponse(data){
		let rememberPassword = document.getElementById("rememberPasswordCheck").checked;
		if(data.code===200){
			PersistData.SetCookieValue("token",data.data.value,rememberPassword?365:0);
			window.location.href = "/workspace"; 
		}else{
			document.getElementById('loadingImage').style.display='none';
			if(data.message==="WRONG_CREDENTIALS"){
				document.getElementById('worngCredentialsMsg').style.display = "block";
			}
			else{
				document.getElementById('unExpectedErrorMsg').style.display = "block";
			}
		}	
	}

	errorCallerBackend(message){
		document.getElementById('loadingImage').style.display='none';
		document.getElementById('unExpectedErrorMsg').style.display = "block";
	}

	render() {
		return (
			<div class="row justify-content-center">
			<div class="col-lg-5">
				<div id="loadingImage" class="modalWindow">
					<div class="modalWindow-content">
						<img  src={require('../../assets/images/ajax-loader01.gif')} alt="" />
    				</div>
				</div>
				<div class="card shadow-lg border-0 rounded-lg mt-5">
					<div class="card-header"><h3 id="lblFormTitle" class="text-center font-weight-light my-4"><FormattedMessage id="login.lblFormTitle" defaultMessage="Acceder" /></h3></div>
					<div class="card-body">
						<form autocomplete="off">
							<div class="form-group">
								<label id="lblinputEmailAddress" class="small mb-1" for="inputEmailAddress"><FormattedMessage id="login.lblinputEmailAddress" defaultMessage="Correo electr&oacute;nico" /></label>
								<input class="form-control py-4" id="inputEmailAddress" autofocus type="email" placeholder="Enter email address" onChange={this.handleChange} autocomplete="off" />
							</div>
							<div class="form-group">
								<label id="lblinputPassword" class="small mb-1" for="inputPassword"><FormattedMessage id="login.lblinputPassword" defaultMessage="Contraseña" /></label>
								<input class="form-control py-4" id="inputPassword" type="password" placeholder="Enter password" onChange={this.handleChange} autocomplete="new-password" />
							</div>
							<div class="form-group">
								<div class="custom-control custom-checkbox">
									<input class="custom-control-input" id="rememberPasswordCheck" type="checkbox" />
									<label id="lblrememberPasswordCheck" class="custom-control-label" for="rememberPasswordCheck"><FormattedMessage id="login.lblrememberPasswordCheck" defaultMessage="Recordar tu contrase&ntilde;a" /></label>
								</div>
							</div>
							<div class="form-group d-flex align-items-center justify-content-between mt-4 mb-0">
								<a id="lnkGoRecoveryPassword" class="small" href="../recoveryPassword"><FormattedMessage id="login.lnkGoRecoveryPassword" defaultMessage="Olvidaste tu contrase&ntilde;a" /></a>
								<button  id="btnSubmit" className="btn btn-primary" onClick={this.submitButtonClickHndlr}><FormattedMessage id="login.btnSubmit" defaultMessage="Enviar" /></button>
							</div>
						</form>
					</div>
					<div class="card-footer text-center">
						<div class="small">
							<a id="lblGotoRegister" href="../Register"><FormattedMessage id="login.lblGotoRegister" defaultMessage="¿Necesitas una cuenta? Reg&iacute;strate!" /></a>
						</div>
					</div>
					<div class="alert alert-danger collapse" role="alert" id="emailEmptyMsg">
						<FormattedMessage id="login.emailEmptyMsg" defaultMessage="Por favor escriba su cuenta de e-mail" />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="passwordEmptyMsg">
						<FormattedMessage id="login.passwordEmptyMsg" defaultMessage="Por favor escriba su contrase&ntilde;a" />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="worngCredentialsMsg">
						<FormattedMessage id="login.worngCredentialsMsg" defaultMessage="El usuario o la contrase&ntilde;a son incorrectos" />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="unExpectedErrorMsg">
						<FormattedMessage id="login.unExpectedErrorMsg" defaultMessage="Hubo un error inesperado :( Por favor intente más tarde" />
					</div>
					<div class="alert alert-success collapse" role="alert" id="successMsg">
						<strong>Success!</strong> It needs a URL to redirect, modify login.js, SubmitForm method.
					</div>
				</div>
			</div>
			</div>
		);
	}
}
export default LoginForm;