import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';
import 'bootstrap/dist/css/bootstrap.css';
import AutocompleteTextBox from '../Autocomplete/AutocompleteTextBox';
import CommonFunctions from '../../helpers/CommonFunctions';
import RestCaller from '../../helpers/RestCaller';
import { urlRegister, urlGetAllAreas, urlGetAllOrganizations } from '../../helpers/external-vars';

class RegisterForm extends Component {

	constructor(props){
		super(props);
		this.state = {
			inputFirstName:"",
			inputLastName:"",
			inputOrganization:"",
			inputEmailAddress:"",
			inputArea:"",
			inputPhoto:"",
			inputPassword:"",
			inputConfirmPassword:"",
			allAreas:[],
			allOrgz:[]
		};
		//Get all areas
		let Arguments = '{"platform":"web","caller":"' + CommonFunctions.GetCaller() + '"}';
		RestCaller.Fetch(urlGetAllAreas, Arguments, 'POST', (responseData)=>{
			this.setState({ allAreas: responseData.data.map(obj => {
				return obj.name
			})});
		}, (error) =>{
			this.fillListsError(error);
		});
		//Get all organizations
		RestCaller.Fetch(urlGetAllOrganizations, Arguments, 'POST', (responseData)=>{
			this.setState({ allOrgz:  responseData.data.map(obj => {
				return obj.name
			})});
		}, (error) =>{
			this.fillListsError(error);
		});
		this.submitButtonClickHndlr = this.submitButtonClickHndlr.bind(this);
	}


	fillListsError(error){
		document.getElementById('unexpectedErrorMsg').style.display = "block";
		document.getElementById('btnSubmit').style.display = "none";
	}

	cleanMessages(){
		document.getElementById('successMsg').style.display = "none";
		document.getElementById('fiilPasswordAndConfirmMsg').style.display = "none";
		document.getElementById('confirmDoesNotMatchMsg').style.display = "none";
		document.getElementById('needAllTheFieldmMsg').style.display = "none";
		document.getElementById('unexpectedErrorMsg').style.display = "none";
		document.getElementById('lengthWronghMsg').style.display = "none";
		document.getElementById('passwordWrongFormat').style.display = "none";
		document.getElementById('emailWrongFormat').style.display = "none";
		document.getElementById('emailAlreadyExistMsg').style.display = "none";
	}

	handleChange = event => {
		event.preventDefault();
		const { id, value } = event.target;
		switch(id){
			case "inputFirstName":
				this.setState({
					inputFirstName: value,
				});
				break;
			case "inputLastName":
				this.setState({
					inputLastName: value,
				});
				break;
			case "inputOrganization":
				this.setState({
					inputOrganization: value
				});
				break;
			case "inputArea":
				this.setState({
					inputArea: value,
				});
				break;
			case "inputPassword":
				this.setState({
					inputPassword: value
				});
				break;
			case "inputConfirmPassword":
				this.setState({
					inputConfirmPassword: value
				});
				break;
			case "inputEmailAddress":
				this.setState({
					inputEmailAddress:value
				});
				break;
			case "inputPhoto":
				this.setState({
					inputPhoto:event.target.files[0]
				});
				document.getElementById('imgPhoto').src = URL.createObjectURL(event.target.files[0]);
				break;
			default:

		}
	}

	validation(){
		this.cleanMessages();
		const {inputFirstName,inputLastName,inputEmailAddress,inputOrganization,inputArea,inputPassword,inputConfirmPassword} = this.state;
		if(inputFirstName.trim().length===0||inputLastName.trim().length===0||
		inputEmailAddress.trim().length===0||inputOrganization.trim().length===0||
		inputArea.trim().length===0||inputPassword.trim().length===0)
		{
			document.getElementById('needAllTheFieldmMsg').style.display = "block";
			return false;
		}
		if(!CommonFunctions.EmailFormatMatch(inputEmailAddress)){
			document.getElementById('emailWrongFormat').style.display = "block";
			return false;
		}
		if(!CommonFunctions.PasswordFormatMatch(inputPassword)){
			document.getElementById('passwordWrongFormat').style.display = "block";
			return false;
		}
		if(inputPassword!=inputConfirmPassword){
			document.getElementById('confirmDoesNotMatchMsg').style.display = "block";
			return false;
		}
		return true;
	}

	submitButtonClickHndlr(event){
		event.preventDefault();
		if(!this.validation()){
			return false;
		}
		document.getElementById('loadingImage').style.display='block';
		const {inputFirstName,inputLastName,inputEmailAddress,inputOrganization,inputArea,inputPhoto,inputPassword,inputConfirmPassword} = this.state;
		const data = new FormData();
		data.append('inputPhoto',inputPhoto, inputPhoto.name);
		data.append('platform','web');
		data.append('caller',CommonFunctions.GetCaller());
		data.append('name',inputFirstName);
		data.append('lastname',inputLastName);
		data.append('organization',inputOrganization);
		data.append('area',inputArea);
		data.append('email',inputEmailAddress);
		data.append('password',inputPassword);
		//Get all organizations
		RestCaller.FetchMultipart(urlRegister, data, 'POST', (responseData)=>{
			//Success
			document.getElementById('loadingImage').style.display='none';
			const resultSet = responseData;
			if(resultSet.code===200){
				document.getElementById('successMsg').style.display = "block";
				document.getElementById('btnSubmit').style.display = "none";
			}else{
				if(data.message==="WRONG_CREDENTIALS"){
					document.getElementById('confirmDoesNotMatchMsg').style.display = "block";
				}else{
					document.getElementById('unexpectedErrorMsg').style.display = "block";
					alert("register Success: " + data.message);
				}
			}	
		}, (error) =>{
			alert("error Success");
			document.getElementById('loadingImage').style.display='none';
			document.getElementById('unexpectedErrorMsg').style.display = "none";
			document.getElementById('btnSubmit').style.display = "block";
		});

	}

	render() {
		const {allAreas, allOrgz } = this.state;
		return (
			<div class="row justify-content-center">
			<div class="col-lg-7">
				<div id="loadingImage" class="modalWindow">
					<div class="modalWindow-content">
						<img  src={require('../../assets/images/ajax-loader01.gif')} alt="" />
    				</div>
				</div>
				<form autocomplete="off">
				<div class="card shadow-lg border-0 rounded-lg mt-5">
					<div class="card-header"><h3 id="lblFormTitle" class="text-center font-weight-light my-4"><FormattedMessage id="regUser.lblFormTitle" defaultMessage="Crea tu cuenta" /></h3></div>
					<div class="card-body">
							<div class="form-row">
								<div class="col-md-6">
									<div class="form-group">
										<label class="small mb-1" for="inputFirstName" id="lblinputFirstName"><FormattedMessage id="regUser.lblinputFirstName" defaultMessage="Nombre" />&nbsp;<font color='red'><b>*</b></font></label>
										<input class="form-control py-4" autoFocus id="inputFirstName" type="text" maxlength="50" placeholder="Enter first name" autocomplete="false" onChange={this.handleChange} />
									</div>
								</div>
								<div class="col-md-6">
									<div class="form-group"><label class="small mb-1" for="inputLastName" id="lblinputLastName"><FormattedMessage id="regUser.lblinputLastName" defaultMessage="Apellidos" />&nbsp;<font color='red'><b>*</b></font></label>
									<input class="form-control py-4" id="inputLastName" type="text" maxlength="50" placeholder="Enter last name" autocomplete="false" onChange={this.handleChange} />
								</div>
							</div>
							</div>
							<div class="form-row">
								<div class="col-md-6">
									<div class="form-group"><label class="small mb-1" for="inputOrganization" id="lblinputOrganization"><FormattedMessage id="regUser.lblinputOrganization" defaultMessage="Organización" />&nbsp;<font color='red'><b>*</b></font></label>
										<AutocompleteTextBox idHook="inputOrganization" maxLength="50" items={allOrgz} onChange={this.handleChange}/>
									</div>
								</div>
								<div class="col-md-6">
									<div class="form-group"><label class="small mb-1" for="inputArea" id="lblinputArea"><FormattedMessage id="regUser.lblinputArea" defaultMessage="Área" />&nbsp;<font color='red'><b>*</b></font></label>
										<AutocompleteTextBox idHook="inputArea" maxLength="50" items={allAreas} onChange={this.handleChange}/>
									</div>
								</div>
							</div>
							<div class="form-group"><label class="small mb-1" for="inputEmailAddress" id="lblinputEmailAddress"><FormattedMessage id="regUser.lblinputEmailAddress" defaultMessage="Email" /></label><input class="form-control py-4" id="inputEmailAddress" maxlength="150" type="email" aria-describedby="emailHelp" placeholder="Enter email address" autocomplete="off" onChange={this.handleChange} /></div>
								<div class="form-row">
									<div class="col-md-6">
										<div class="form-group"><label class="small mb-1" for="inputPhoto" id="lblPhoto"><FormattedMessage id="regUser.lblinputPhoto" defaultMessage="Foto" /></label><input class="form-control-file py-4" id="inputPhoto" name="inputPhoto" type="file" aria-describedby="Photo" placeholder="Photo" accept="image/x-png,image/gif,image/jpeg" onChange={this.handleChange} /></div>
									</div>
									<div class="col-md-6">
										<div class="form-group" style={{verticalAlign: "middle",textAlign:"center"}}><img id="imgPhoto" style={{borderRadius: "10px",width:"42px",height:" 56px"}} src={require('../../assets/images/anonymous.png')} alt="" /></div>
									</div>
								</div>
							<div class="form-row">
								<div class="col-md-6">
									<div class="form-group"><label class="small mb-1" for="inputPassword" id="lblinputPassword"><FormattedMessage id="regUser.lblinputPassword" defaultMessage="Contraseña" />&nbsp;<font color='red'><b>*</b></font></label><input class="form-control py-4" id="inputPassword" type="password" maxlength="15" placeholder="Enter password" autocomplete="new-password" onChange={this.handleChange} /></div>
								</div>
								<div class="col-md-6">
									<div class="form-group"><label class="small mb-1" for="inputConfirmPassword" id="lblinputConfirmPassword"><FormattedMessage id="regUser.lblinputConfirmPassword" defaultMessage="Confirma tu Contraseña" />&nbsp;<font color='red'><b>*</b></font></label><input class="form-control py-4" id="inputConfirmPassword" type="password" maxlength="15" placeholder="Confirm password" onChange={this.handleChange} /></div>
								</div>
							</div>
							<div class="form-group mt-4 mb-0">
								<button  id="btnSubmit" className="btn btn-primary" onClick={this.submitButtonClickHndlr}>
									<FormattedMessage id="regUser.btnSubmit" defaultMessage="Crear" />
								</button>
							</div>
						<div class="alert alert-danger collapse" role="alert" id="needAllTheFieldmMsg">
							<FormattedMessage id="regUser.needAllTheFieldmMsg" defaultMessage="Por favor llene todos los campos marcados." />
						</div>
						<div class="alert alert-danger collapse" role="alert" id="fiilPasswordAndConfirmMsg">
							<FormattedMessage id="regUser.fiilPasswordAndConfirmMsg" defaultMessage="Por favor llena el campo Contraseña y confirma la contraseña." />
						</div>
						<div class="alert alert-danger collapse" role="alert" id="confirmDoesNotMatchMsg">
							<FormattedMessage id="regUser.confirmDoesNotMatchMsg" defaultMessage="La contraseña y la confirmación no coinciden." />
						</div>
						<div class="alert alert-danger collapse" role="alert" id="emailWrongFormat">
							<FormattedMessage id="regUser.emailWrongFormat" defaultMessage="El e-mail no tiene el formato correcto." />
						</div>
						<div class="alert alert-danger collapse" role="alert" id="emailAlreadyExistMsg">
							<a href='./RecoveryPwd'><FormattedMessage id="regUser.emailAlreadyExistMsg" defaultMessage="La cuenta de correo electrónico ya existe ¿necesitas recuperar tu contrase&ntilde;a? Da clic aquí." /></a>
						</div>
						<div class="alert alert-danger collapse" role="alert" id="lengthWronghMsg">
							<FormattedMessage id="regUser.lengthWronghMsg" defaultMessage="La contraseña por lo menos debe tener al menos 8 carateres." />
						</div>
						<div class="alert alert-danger collapse" role="alert" id="passwordWrongFormat">
							<FormattedMessage id="regUser.passwordWrongFormat" defaultMessage="La contraseña debe tener por lo menos 8 caracteres, contener por lo menos una letra minúscula, contener por lo menos una letra mayúscula y contener por lo menos un número." />
						</div>
						<div class="alert alert-danger collapse" role="alert" id="unexpectedErrorMsg">
							<FormattedMessage id="regUser.unexpectedErrorMsg" defaultMessage="Ocurrio un error inesperado, por favor, vuelve a intentar más tarde." />
						</div>
						<div class="alert alert-success collapse" role="alert" id="successMsg">
							<a href='./Login'><FormattedMessage id="regUser.successMsg" defaultMessage="Tu registro se realizó con éxito, da click aquí para proporcionar tus credenciales." /></a>
						</div>
					</div>
					<div class="card-footer text-center">
						<div class="small"><a id="lnkGotoLogin" href="./Login"><FormattedMessage id="regUser.lblFormTitle" defaultMessage="Acceder" /></a></div>
					</div>
				</div>
				</form>
			</div>
		</div>
		);
	}
}
export default RegisterForm;
