import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';
import CommonFunctions from '../../helpers/CommonFunctions';
import RestCaller from '../../helpers/RestCaller';
import { ulrRecoveryPassword } from '../../helpers/external-vars';

class RecoveryPasswordForm extends Component {

	constructor(props){
		super(props);
		this.state={
			emailAccount:"",
		};
		this.submitButtonClickHndlr = this.submitButtonClickHndlr.bind(this);
	}

	handleChange = e => {
		e.preventDefault();
		const { value } = e.target;
		this.setState(() => ({
			emailAccount: value,
		}));		
	}

	submitButtonClickHndlr(event){
		event.preventDefault();
		const { emailAccount } = this.state;
		document.getElementById('successMsg').style.display = "none";
		document.getElementById('fillTheEmailMsg').style.display = "none";
		document.getElementById('unexpectedErrorMsg').style.display = "none";
		document.getElementById('emailDoesNotExistMsg').style.display = "none";
		if(!CommonFunctions.EmailFormatMatch(emailAccount)){
			document.getElementById('fillTheEmailMsg').style.display = "block";
			return;
		}
		document.getElementById('loadingImage').style.display='block';
		let Arguments = '{"platform":"web","caller":"' + CommonFunctions.GetCaller() + '","username":"' + document.getElementById("inputEmailAddress").value.trim() + '"}';
		RestCaller.Fetch(ulrRecoveryPassword, Arguments, 'POST', (responseData)=>{
			//Success
			this.evalSuccessResponse(responseData);
		}, (error) =>{
			this.errorCallerBackend(error);
		});
	}

	evalSuccessResponse(data){
		document.getElementById('loadingImage').style.display='none';
		if(data.code===200){
			document.getElementById('btnSubmit').style.display='none';
			document.getElementById('successMsg').style.display = "block";
		}else{
			if(data.message==="WRONG_CREDENTIALS"){
				document.getElementById('worngCredentialsMsg').style.display = "block";
			}
			else{
				document.getElementById('unExpectedErrorMsg').style.display = "block";
			}
		}
	}

	errorCallerBackend(message){
		document.getElementById('loadingImage').style.display = "none";
		document.getElementById('unexpectedErrorMsg').style.display = "block";
	}

	render(){
		return(
			<div class="row justify-content-center">
			<div class="col-lg-5">
				<div id="loadingImage" class="modalWindow">
					<div class="modalWindow-content">
						<img  src={require('../../assets/images/ajax-loader01.gif')} alt="" />
    				</div>
				</div>
				<div class="card shadow-lg border-0 rounded-lg mt-5">
					<div class="card-header"><h3 id="lblFormTitle" class="text-center font-weight-light my-4"><FormattedMessage id="recovPwd.lblFormTitle" defaultMessage="Recupera tu contraseña" /></h3></div>
					<div class="card-body">
						<div id="lblEnterEmail" class="small mb-3 text-muted"><FormattedMessage id="recovPwd.lblEnterEmail" defaultMessage="Escribe tu correo electrónico y te enviaremos una liga para reestablecer tu contraseña." /></div>
						<form>
							<div class="form-group"><label id="lblinputEmailAddress" class="small mb-1" for="inputEmailAddress">
								<FormattedMessage id="recovPwd.lblinputEmailAddress" defaultMessage="Correo electrónico" /></label>
								<input class="form-control py-4" id="inputEmailAddress" autoFocus type="email" aria-describedby="emailHelp" onChange={this.handleChange} placeholder="Enter email address" />
							</div>
							<div class="form-group d-flex align-items-center justify-content-between mt-4 mb-0">
								<a id="lnkGotoLogin" class="small" href="./login"><FormattedMessage id="recovPwd.lnkGotoLogin" defaultMessage="Regresar a Acceder" /></a>
								<button  id="btnSubmit" className="btn btn-primary" onClick={this.submitButtonClickHndlr}>
									<FormattedMessage id="recovPwd.btnSubmit" defaultMessage="Enviar" />
								</button>
							</div>
						</form>
					</div>
					<div class="card-footer text-center">
						<div class="small"><a id="lblGotoRegister" href="./register"><FormattedMessage id="recovPwd.lblGotoRegister" defaultMessage="¿Necesitas una cuenta? ¡Regístrate!" /></a></div>
					</div>
					<div class="alert alert-danger collapse" role="alert" id="emailDoesNotExistMsg">
						<FormattedMessage id="recovPwd.emailDoesNotExistMsg" defaultMessage="El correo electrónico que proporcionaste no se encuentra en nuestra base de datos." />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="fillTheEmailMsg">
						<FormattedMessage id="recovPwd.fillTheEmailMsg" defaultMessage="Por favor escribe tu cuenta de  correo electrónico con que te registrasté." />
					</div>
					<div class="alert alert-danger collapse" role="alert" id="unexpectedErrorMsg">
						<FormattedMessage id="recovPwd.unexpectedErrorMsg" defaultMessage="Ocurrió un error inesperado, por favor, vuelve a intentar más tarde." />
					</div>
					<div class="alert alert-success collapse" role="alert" id="successMsg">
						<FormattedMessage id="recovPwd.successMsg" defaultMessage="Te enviamos un correo electrónico para que puedas recuperar tu contraseña." />
					</div>
				</div>
			</div>
		</div>
		);
	}
}

export default RecoveryPasswordForm;