import React, { useContext} from 'react';
import { Nav, Navbar, Form } from 'react-bootstrap';
import styled from 'styled-components';
import NavbarToggle from 'react-bootstrap/esm/NavbarToggle';
import { FormattedMessage} from 'react-intl';
import { Context } from './Wrapper';
import Spanish from '../languages/es.json';
import English from '../languages/en.json';


export function NavigationBar(){
	const context = useContext(Context);
	const Styles = styled.div`
	.navbar {
		background-color: #222;

	}

	.navbar-brand, .navbar-nav .nav-link {
		color: #bbb;

		&:hover{
			color: white;
		}
	}
`;

	return(
		<Styles>
			<Navbar expand="lg">
				<Navbar.Brand href="/">Köatchy</Navbar.Brand>
				<NavbarToggle aria-controls="basic-navbar-nav"></NavbarToggle>
				<Navbar.Collapse id="basic-navbar-nav">
					<Nav className="ml-auto">
						<Nav.Item><Nav.Link href="/"><FormattedMessage id="navBar.Home" defaultMessage="Inicio" /></Nav.Link></Nav.Item>
						<Nav.Item><Nav.Link href="/about"><FormattedMessage id="navBar.AboutOf" defaultMessage="Acerca de" /></Nav.Link></Nav.Item>
						<Nav.Item><Nav.Link href="/contact"><FormattedMessage id="navBar.Contact" defaultMessage="Contacto" /></Nav.Link></Nav.Item>
						<Nav.Item><Nav.Link href="/login"><FormattedMessage id="navBar.Login" defaultMessage="Acceder" /></Nav.Link></Nav.Item>
					</Nav>
				</Navbar.Collapse>
				<Form inline>
					<div className="navbar">&nbsp;Idioma&nbsp;</div>
					<select value={context.local} onChange={context.selectLang}>
						<option value="en-US">English</option>
						<option value="es-MX">Español</option>
					</select>
				</Form>
			</Navbar>
		</Styles>

	)
}

