import React, {Component} from 'react';
import './AutocompleteTextBox.css';

export default class AutocompleteTextBox extends Component {

	constructor(props){
		super(props);
		this.state = {
			suggestions:[],
			text:''
		};
	}

	onTextChanged = (event) => {
		const { items } = this.props;
		const value = event.target.value;
		let suggestions = [];
		if(value.length > 0){
			const regex = new RegExp(`^${value}`, 'i');			
			suggestions = items.sort().filter(v => regex.test(v));
		}
		this.setState(() => ({ suggestions, text: value }));
		this.props.onChange(event);
	}

	suggestionSelected(value){
		this.setState(() => ({
			text: value,
			suggestions:[],
		}));
	}

	renderSuggestions(){
		const { suggestions } = this.state;
		if(suggestions.length === 0){
			return null;
		}
		return (
			<ul>
				{suggestions.map((item)=><li onClick={() => this.suggestionSelected(item)}>{item}</li>)}
			</ul>
		);
	}

	render(){
		const { text } = this.state;
		const { idHook } = this.props;
		const { maxLength } = this.props;
		return(
			<div className="AutocompleteTextBox">
				<input type="text" id={idHook} maxLength={maxLength} value={text} onChange={this.onTextChanged}  autocomplete="off" />
				{this.renderSuggestions()}
			</div>
		)
	}


}